<template>
    <div class="detail-root">

        <title-bar :back="true" :title="'开奖详情'" @onBack="onBack"></title-bar>

        <div class="detail-content">

            <swiper-pic></swiper-pic>

            <lottery-info-item :data="data"></lottery-info-item>
            <lottery-money :data="data"></lottery-money>
            <lottery-result :data="data"></lottery-result>

        </div>

    </div>
</template>

<script>
    import TitleBar from "../components/TitleBar";
    import SwiperPic from "../components/SwiperPic";
    import LotteryInfoItem from "../components/LotteryInfoItem";
    import LotteryMoney from "../components/LotteryMoney";
    import LotteryResult from "../components/LotteryResult";

    export default {
        name: "LotteryDetailPage",
        components: {LotteryResult, LotteryMoney, LotteryInfoItem, SwiperPic, TitleBar},
        data() {
            return {
                data: null
            }
        },
        mounted() {
            this.data = this.$route.params.data
        },
        methods: {
            onBack() {
                this.$router.go(-1)
            }
        }
    }
</script>

<style scoped>

    .detail-root {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .detail-content {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
    }

</style>