<template>

    <div class="info-item" v-if="data != null">

        <div class="info-title">
            <span class="info-title-main">第{{data.code}}期</span>
            <span class="info-title-sub">{{date}}(星期{{data.week}})</span>
        </div>
        <div class="ball-container">

            <div class="ball" v-for="ball in redBalls">
                {{ball}}
            </div>
            <div class="ball blue">{{blueBall}}</div>

        </div>

    </div>

</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        name: "LotteryInfoItem",
        components: {Icon},
        props: {
            data: {
                required: true,
                default: null
            }
        },
        computed: {
            redBalls() {
                return this.data.red.split(",")
            },
            blueBall() {
                return this.data.blue
            },
            date() {
                const date = this.data.date
                return date.substring(0,date.indexOf("("))
            }
        }
    }
</script>

<style scoped>

    .info-item {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 100%;
        display: flex;
        flex-direction: column;
        padding: 1rem;
        justify-content: center;
        position: relative;
    }

    .info-title {
        display: flex;
        justify-content: space-between;
    }

    .info-title-main {
        font-size: 1.2rem;
    }

    .info-title-sub {
        font-size: 1.2rem;
        margin-right: 2rem;
    }

    .ball-container {
        margin-top: .8rem;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-right: 2rem;
    }


    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(229, 28, 35, 1);
        border-radius: 1.5rem;
        margin-right: .4rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.blue {
        color: rgba(0, 0, 255, 1);
        border: .12rem solid rgba(0, 0, 255, 1);
    }

</style>