<template>

    <div class="money-root">

        <table class="money-table">

            <tr>
                <td class="head">本期销量</td>
                <td class="head">奖池奖金</td>
            </tr>
            <tr v-if="data != null">
                <td class="content">{{sales}}</td>
                <td class="content">{{poolmoney}}</td>
            </tr>

        </table>

    </div>

</template>

<script>
    export default {
        name: "LotteryMoney",
        props: {
            data: {
                required: true,
                default: null
            }
        },
        computed: {
            sales() {
                const str = this.data.sales.toString()
                return this.toMoney(str)
            },
            poolmoney() {
                const str = this.data.poolmoney.toString()
                return this.toMoney(str)
            }
        },
        methods: {
            toMoney(str) {
                let arr = []
                let count = 0;
                for(let i = str.length - 1; i >= 0; i--) {
                    count++
                    arr.push(str[i])
                    if (count % 3 == 0 && i != 0) {
                        arr.push(",")
                    }
                }

                let result = ""
                arr.forEach(function (item) {
                    result = item + result
                })
                return result
            }
        }
    }
</script>

<style scoped>

    .money-root {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .money-table td {
        width: 50%;
        border: .2rem solid #33333333;
    }

    .money-table .head {
        background-color: #33333333;
        height: 2.5rem;
        font-size: 1.5rem;
    }

    .money-table .content {
        height: 2.5rem;
        font-size: 1.5rem;
    }

    .money-table {
        width: 94%;
        text-align: center;
        border: .2rem solid #33333333;
        border-collapse: collapse;
    }

</style>