<template>
    <div class="result-root">

        <table class="result-table" v-if="data != null">

            <tr>
                <td class="head">奖项</td>
                <td class="head">中奖注数</td>
                <td class="head">每注奖金</td>
            </tr>

            <tr v-if="data.prizegrades != undefined && data.prizegrades != null" v-for="item in data.prizegrades">
                <td class="content">{{toIndex(item.type)}}等奖</td>
                <td class="content">{{item.typenum}}</td>
                <td class="content">{{toMoney(item.typemoney)}}</td>
            </tr>

        </table>


    </div>
</template>

<script>
    export default {
        name: "LotteryResult",
        props: {
            data: {
                required: true,
                default: null
            }
        },
        methods: {
            toIndex(num) {
                const types = {
                    1: "一",
                    2: "二",
                    3: "三",
                    4: "四",
                    5: "五",
                    6: "六"
                }
                return types[num.toString()]
            },
            toMoney(data) {
                const str = data.toString()
                let arr = []
                let count = 0;
                for (let i = str.length - 1; i >= 0; i--) {
                    count++
                    arr.push(str[i])
                    if (count % 3 == 0 && i != 0) {
                        arr.push(",")
                    }
                }

                let result = ""
                arr.forEach(function (item) {
                    result = item + result
                })
                return result
            }
        }
    }
</script>

<style scoped>


    .result-root {
        margin-top: 1rem;
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .result-table td {
        width: 33.333%;
        border: .2rem solid #33333333;
    }

    .result-table .head {
        background-color: #33333333;
        height: 2.5rem;
        font-size: 1.2rem;
    }

    .result-table .content {
        height: 2.5rem;
        font-size: 1.2rem;
    }

    .result-table {
        width: 94%;
        text-align: center;
        border: .2rem solid #33333333;
        border-collapse: collapse;
    }

</style>